body {
    font-family: Sen,sans-serif;
    color: white;
}

h1, h2, h3, h4, h5, h6 {
    font-family: Sen,sans-serif;
    font-weight: 700;
    -webkit-font-smoothing: antialiased;
    -webkit-text-size-adjust: 100%;
    color: white;
}

p {
    font-family: Sen,sans-serif;
    font-weight: 400;
    -webkit-font-smoothing: antialiased;
    -webkit-text-size-adjust: 100%;
    color: white;
}

a {
    text-decoration: none !important;
}

.button, input[type=submit] {
    font-family: 'Sen', sans-serif;
    font-weight: 400;
    letter-spacing: normal;
    text-transform: uppercase;
    font-size: 1.2rem;
    line-height: 1.2727272727;
    display: inline-flex;
    height: 4.125rem;
    align-content: center;
    align-items: center;
    vertical-align: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    cursor: pointer;
    text-align: center;
    justify-content: center;
    border-width: 0;
    border-radius: 0;
    background-color: #ffffff;
    color: #030200;
}

.button.is-primary, .is-contrasted .button.is-primary {
    width: 150pt;
    border: 1px solid black;
}

.button.is-dark, .is-contrasted .button.is-dark {
    width: 150pt;
    background-color: black;
    color: white;
}

.button.is-primary:hover, .button.is-dark:hover {
    opacity: .8;
}

.button-introduction-vertical-center {
    min-height: 100%;  /* Fallback for browsers do NOT support vh unit */
  
    display: flex;
    align-items: center;
    justify-content: center;
}

@media screen and (max-width: 769px) {
    .button-introduction-vertical-center {
        margin-top: 20px;
    }
}

.introduction-heading {
    text-transform: uppercase;
}

.introduction-container {
    background-color: rgba(0,0,0,0.9);
    padding: 50px;
}

.introduction-background {
    padding-top: 260px;
    background-size: cover;
    background-image: url("./img/introduction-background.png");
    background-repeat: no-repeat;
    background-position: center;
}

.question-heading {
    text-shadow: 0px 0px 25px rgba(0, 0, 0, .7);

    min-height: 100%;  /* Fallback for browsers do NOT support vh unit */
  
    display: flex;
    align-items: center;
}

.question-circle {
    height: 11vw;
    width: 11vw;
    border: 8px solid white;
    border-radius: 50%;
    display: inline-block;
    font-size: 4vw;
    font-weight: 700;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: black;
    -webkit-box-shadow: 0px 0px 30px 0px rgba(0,0,0,0.2);
    -moz-box-shadow: 0px 0px 30px 0px rgba(0,0,0,0.2);
    box-shadow: 0px 0px 30px 0px rgba(0,0,0,0.2);
}

@media only screen and (max-width: 769px) {
    .question-circle {
        width: 20vw;
        height: 20vw;
        max-width: 115px;
        max-height: 115px;
        font-size: 6vw;
    }
}

.question-circle__col {
    display: flex;
    align-items: center;
    justify-content: center;
}

.questionmargin {
    margin-bottom: 50px;
}

.questionmargin-row {
    margin-top: 100px;
}

@media only screen and (max-width: 991px) {
    .questionmargin-row {
        margin-top: -20px;
    }
}

.skintone-container {
    background-color: rgba(0,0,0,1);
    padding: 50px;
}

.skintone-rowHeadingBackground {
    background-color: white;
    padding: 15px;
}

.skintone-rowHeadingText {
    text-transform: uppercase;
    color: black;
    margin-bottom: 0;
}

@media only screen and (min-width: 769px) {
    .skintone-shades {
        padding: 0 90px;
    }
}

.skintone-shade {
    margin-bottom: 50px;
}

.skintone-selected {
    border: solid 7px black;
}

@media only screen and (max-width: 769px) {
    .skintone-desktop {
        display: none;
    }
    .model-img {
        padding-top: 20px;
    }
}

@media only screen and (min-width: 769px) {
    .skintone-mobile {
        display: none;
    }
}

.container-btn {
    position: relative;
}

.btn-circle {
    height: 70px;
    width: 70px;
    background-color: white;
    border-radius: 50%;
    display: inline-block;
    font-size: 1em;
    padding-top: 19px;
    font-weight: 700;
    line-height: 17px;
    color: black;
    cursor: pointer;
    text-transform: uppercase;
    -webkit-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.5);
    -moz-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.5);
    box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.5);
}

.btn-circle:hover {
    opacity: .8;
}

.btnHolder {
    position: fixed;
    margin-left: -65px;
    padding: 15px 35px;
    bottom: 0;
}

@media (min-width: 768px) {
    .btnHolder {
        position: absolute;
    }
}

.btn-fixed {
    position: fixed;
}

@media (max-width: 576px) {
    .btnHolder {
        width: 100%;
    }
}

@media (min-width: 576px) {
    .btnHolder {
        width: 540px;
    }
}

@media (min-width: 768px) {
    .btnHolder {
        width: 720px;
    }
}

@media (min-width: 992px) {
    .btnHolder {
        width: 960px;
    }
}

@media (min-width: 1200px) {
    .btnHolder {
        width: 1140px;
    }
}

.btnHolder .back {
    float: left;
    left: 0;
}

.btnHolder .next {
    float: right;
    right: 0;
}

.btnBack, .btnNext {
    outline: none;
}

.question-container {
    background: rgba(0,0,0,1);
    background-size: cover;
    padding: 50px 50px 100px 50px;
}

.question1-background {
    background-image: linear-gradient(0deg, rgba(0,0,0,.8) 2%, rgba(0, 0, 0, 0)), url('./img/question1-background.jpg');
    background-position: 70% center;
}

.question2-background {
    background-image: linear-gradient(0deg, rgba(0,0,0,.8) 2%, rgba(0, 0, 0, 0)), url('./img/question2-background.jpg');
    background-position: center 65%;
}

.question3-background {
    background-image: linear-gradient(0deg, rgba(0,0,0,.8) 2%, rgba(0, 0, 0, 0)), url('./img/question3-background.jpg');
    background-position: center;
}

.answer-option {
    background-color: white;
    width: 10vw;
    height: 10vw;
    min-width: 120px;
    min-height: 120px;
    text-align: center;
    cursor: pointer;
    padding: 1vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

@media only screen and (min-width: 769px) {
    .answer-option:hover {
        background-color: black;
        border: 6px solid white;
    }
}

@media only screen and (max-width: 769px) {
    .answer-option {
        width: 100%;
        height: 20vh;
        margin-top: 20px;
    }
}

.answer-option__text {
    color: black !important;
}

.answer-option:hover .answer-option__text {
    color: white !important;
}

.answer-a {
    display: flex;
    justify-content: center;
}

@media screen and (min-width: 769px) {
    .result-text {
        padding: 150px 120px;
    }
}

@media screen and (max-width: 769px) {
    .result-text {
        padding-top: 50px;
        padding-bottom: 50px;
    }
}

.result-image {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
}

.result-heading {
    margin-bottom: 50px;
}

.result-shadename {
    font-size: 3em;
    text-transform: capitalize;
}

.result-desc {
    font-size: 1.3em;
    margin-bottom: 40px;
}

.product-card {
    background-color: white;
    margin-bottom: 30px;
}

@media screen and (min-width: 769px) {
    .product-card__text {
        padding: 150px 100px 150px 0;
    }
}

@media screen and (max-width: 769px) {
    .product-card__text {
        margin-left: 15px;
        margin-right: 15px;
        padding: 30px 30px;
        text-align: center !important;
    }
}

.product-card__text > h1, .product-card__text > h2, .product-card__text > h3, .product-card__text > h4, .product-card__text > h5, .product-card__text > h6, .product-card__text > p {
    color: black;
}

.result-image__spray {
    padding-top: 30px;
}

.result-image__brush {
    padding-top: 10px;
}

.result-border {
    border: 1px solid white;
}

.result-container {
    background-color: rgba(0,0,0,1);
    padding: 50px 50px 90px 50px;
}

.img-skintone {
    cursor: pointer;
}

.img-skintone:hover {
    opacity: 0.8;
}

